/* Barra superior fija */
.top-bar {
  background-color: #894192;
  color: white;
  padding: 20px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000; /* Asegura que esté por encima del contenido */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px; /* Ajusta la altura si es necesario */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

/* Añadir espacio en el contenido para evitar que se superponga con la barra superior */
.content {
  padding-top: 80px; /* Asegura que el contenido no quede debajo de la barra superior */
}


.top-bar-title {
  color: white;
  font-family: 'Poppins', sans-serif;
  font-size: 2rem;
  margin: 0;
}

/* Barra de Navegación Inferior */
.bottom-nav {
  display: flex;
  justify-content: space-around;
  background-color: #894192;
  padding: 10px 0;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  width: 100vw;
  margin: 0;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 1000;
}

.nav-button {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: rgba(255, 255, 255, 0.6);
  background: none;
  border: none;
  font-size: 0.9rem;
  font-family: 'Poppins', sans-serif;
  cursor: pointer;
}

.nav-button img {
  width: 24px;
  height: 24px;
  margin-bottom: 5px;
  opacity: 0.6;
}

.nav-button span {
  margin-top: 3px;
}

.nav-button.active {
  color: #ffffff;
}

.nav-button.active img {
  opacity: 1;
}

/* Ajustes específicos para pantallas más estrechas */
@media (max-width: 480px) {
  .nav-button img {
    width: 20px; /* Ajustar el tamaño del ícono */
    height: 20px;
  }
  
  .nav-button span {
    font-size: 0.7rem; /* Reducir el tamaño del texto */
  }

  .bottom-nav {
    padding: 8px 0; /* Reducir el padding vertical */
  }
}